import { Utility } from './../../Common/utility';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit, ViewChild, ElementRef, AfterViewInit, ViewContainerRef } from '@angular/core';
import * as THREE from 'three';
import ThreeGlobe from 'three-globe';
import * as  TrackballControls from 'three-trackballcontrols';
import { geoOrthographicRaw, geoOrthographic, geoPath, geoTransverseMercator, geoGraticule, geoGraticule10, geoDistance, geoInterpolate } from 'd3-geo';
import { CubicBezierCurve3 } from 'three';
@Component({
    selector: 'app-globe-ele',
    templateUrl: './globe-ele.component.html',
    styleUrls: ['./globe-ele.component.scss']
})
export class GlobeEleComponent implements OnInit, AfterViewInit {

    @ViewChild('webGl', { static: false }) webglEl: ElementRef;

    control;
    renderer;
    scene;
    camera;
    globe;

    constructor(private viewContainerRef: ViewContainerRef, private http: HttpClient) { }

    ngOnInit() {
    }

    ngAfterViewInit() {

        let globalContainer = this.viewContainerRef.element.nativeElement.querySelector('.global-conatiner');

        setTimeout(() => {
            var width = globalContainer.clientWidth,
                height = globalContainer.clientHeight;

            this.renderer = new THREE.WebGLRenderer();
            this.renderer.setSize(width, height);
            this.renderer.setPixelRatio(window.devicePixelRatio);
            globalContainer.appendChild(this.renderer.domElement);

            this.scene = new THREE.Scene();
            this.scene.add(new THREE.AmbientLight(0xbbbbbb, 1));
            this.globe = new ThreeGlobe();


            var projection = geoOrthographic()
                .scale(300)
                .translate([width / 2, height / 2])
                .clipAngle(90)
                .precision(10);

            var mercator = geoTransverseMercator();
            var path = geoPath(projection);
            var ball = geoGraticule();

            var group = new THREE.Group();

            this.http.get('/assets/custom.geo.json').subscribe((result) => {
                Utility.drawThreeGeo(result, 100, 'sphere', {
                    color: '#237148',
                    linewidth: 5
                }, group);

            });

            console.log(this.renderer.getParameter(this.renderer.ALIASED_LINE_WIDTH_RANGE))

            this.scene.add(group);

            var point = this.globe.getCoords(25.0169638, 121.226181);


            // var sphere1 = this.generateDotSphere();
            // this.scene.add(sphere1);

            var geometry = new THREE.SphereGeometry(1, 32, 32);
            var material = new THREE.MeshBasicMaterial({ color: 0xffff00 });
            var sphere = new THREE.Mesh(geometry, material);
            sphere.position.copy(point);
            // sphere.rotation.x = -Math.PI / 2;
            this.scene.add(sphere);

            var startWarp = this.createStars(100, 64);
            this.scene.add(startWarp);


            let line = this.drawCubicBezierCurve3([(Math.random() - 0.5) * 180, (Math.random() - 0.5) * 360], [121.226181, 25.0169638])
            // line.rotation.x = Math.PI / 2;;
            this.scene.add(line);
            group.rotation.x = -Math.PI / 2;
            group.rotation.z = -Math.PI / 2;
            // console.log(q)
            // Setup camera
            this.camera = new THREE.PerspectiveCamera(45, width / height, 1, 10000);

            // this.camera.aspect = width / height;
            this.camera.updateProjectionMatrix();
            this.camera.position.z = 300;

            this.control = new TrackballControls(this.camera, globalContainer);

            this.play();
        });

    }

    play = () => {
        requestAnimationFrame(this.play);
        this.scene.rotation.y += 0.001;
        this.control.update();
        this.renderer.render(this.scene, this.camera);
    }

    generateDotSphere() {
        var group = new THREE.Object3D();

        // 半径
        var radius = 100;
        var separation = 3;

        for (var s = 0; s <= 180; s += separation) {
            // 0 <= s <= 180, なんで -1 <= Math.cos(radianS) <= 1
            // なんで zが -radius <= z <= radius
            var radianS = s * Math.PI / 180;
            var pZ = radius * Math.cos(radianS);

            // 円に沿って点描く
            for (var t = 0; t < 360; t += separation) {
                // 角度をラジアンに
                var radianT = t * Math.PI / 180;
                // 点の座標を計算
                // sin(radianS)は0→1→0の順で変化する
                // radius * sin(radianS)は0→200→0になる
                var pX = radius * Math.sin(radianS) * Math.cos(radianT);
                var pY = radius * Math.sin(radianS) * Math.sin(radianT);

                var geometory = new THREE.SphereGeometry(0.5, 6, 6);
                var material = new THREE.MeshBasicMaterial({
                    color: 0x237148
                });
                var mesh = new THREE.Mesh(geometory, material);
                mesh.position.x = pX;
                mesh.position.y = pY;
                mesh.position.z = pZ;
                group.add(mesh);
            }

        }
        group.rotation.x = -Math.PI / 2;
        return group;
    }

    drawCubicBezierCurve3(startPnt, endPnt) {


        let curves = [startPnt];

        // the three-globe default value.
        let altAutoScale = 0.5
        let altitude = geoDistance(startPnt as [number, number], endPnt as [number, number]) / 2 * altAutoScale;

        const fff = geoInterpolate(startPnt, endPnt);

        Array.from([0.25, 0.75]).forEach((o) => {
            // console.log(o, 'loop')
            let c = fff(o);
            // console.log(c)
            c.push(altitude * 1.5);
            curves.push(c);
            // console.log(this.polar2Cartesian(...c));
        });
        curves.push(endPnt);


        let fg = curves.map(o => {
            return this.globe.getCoords(o[1], o[0], o[2]);
        });

        var curve = new CubicBezierCurve3(fg[0], fg[1], fg[2], fg[3]);

        var points = curve.getPoints(50);
        var geometry = new THREE.BufferGeometry().setFromPoints(points);

        var material = new THREE.LineBasicMaterial({ color: 0xffffff });

        // Create the final object to add to the scene
        var curveObject = new THREE.Line(geometry, material);
        // curveObject.rotation.x = -Math.PI / 2;

        return curveObject;
    }

    createStars(radius, segments) {
        return new THREE.Mesh(
            new THREE.SphereGeometry(radius, segments, segments),
            new THREE.MeshBasicMaterial({
                map: THREE.ImageUtils.loadTexture('/assets/images/galaxy_starfield.png'),
                side: THREE.BackSide
            })
        );
    }
}
